import { useEffect, useRef } from "react"

const CONTENT = [
  { id: 1, desc: '   ', num: <p><span>11,000</span></p> },
  { id: 2, desc: '  24 ', num: <p><span>1</span></p> },
  { id: 3, desc: '   ', num: <p><span>1200</span></p> },
]

const Why = () => {
  const DATA = useRef();
  useEffect(() => {
    window.addEventListener("scroll", () => {
      let sct = window.scrollY
      console.log(sct)
      sct > 380 ? DATA.current.classList.add('on') : DATA.current.classList.remove('on')
    })
  }, [])
  return (
    <section className="Why">
      <div className="container">
        <h2>
            <br />
           <span></span> ?
        </h2>
        <p>*1200(     2022 9 )</p>
        <p className="last">*1 (    24 ,    13 ) </p>
        <div ref={DATA} className="box">
          {
            CONTENT.map((el, idx) => {
              return (
                <div key={el.id}>
                  <p>{el.desc}</p>
                  {el.num}
                </div>
              )
            })
          }
        </div>
      </div>
    </section>
  )
}

export default Why;