import { useEffect, useRef, useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";

const SLIDE = [
  { id: 1, content: <div> <br />  </div>, sub: '   !', link: '/', tab: '' },
  { id: 2, content: <div> <br /> </div>, sub: '  !', link: '/', tab: '' },
  { id: 3, content: <div>  <br />,   </div>, sub: '     !', link: '/', tab: '' }
]

const MainVisual = () => {
  const [IDX, setIDX] = useState();
  useEffect(() => {
    setIDX(0)
  }, [])

  const mainSlide = useRef(null)

  var settings = {
    slideToshow: 1,
    arrows: false,
    autoplay: true,
    autoplaySpeed: 5000,
    speed: 1000,
    pauseOnHover: false,
    pauseOnFocus: false,
    afterChange: index => {
      setIDX(index)
    }
  }

  return (
    <section className="MainVisual">
      <Slider {...settings} ref={mainSlide}>
        {SLIDE.map((el, idx) => {
          return (
            <figure className={`itm0${el.id} ${idx === IDX ? "on" : ""}`} key={el.id}>
              <div className="container">
                <h2>{el.content}</h2>
                <p>{el.sub}</p>
                <a href={el.link}></a>
              </div>
            </figure>
          )
        })}
      </Slider>
      <ul className="slideTab">
        {
          SLIDE.map((el, idx) => {
            return (
              <li key={el.id} className={idx === IDX ? "on" : ""}
                onClick={() => { mainSlide.current.slickGoTo(idx) }}
              >
                {el.tab}</li>
            )
          })
        }
      </ul>

      <div className="control">
        <i className="xi-pause" onClick={() => { mainSlide.current.slickPause() }}></i>
        <i className="xi-play" onClick={() => { mainSlide.current.slickPlay() }}></i>
      </div>
    </section>
  )
}

export default MainVisual;