import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import "swiper/css/effect-coverflow";
import { EffectCoverflow } from "swiper";
import { useEffect, useState } from 'react';

const SLIDE = [
  { id: 1, title: ' ', des: '    PC           .' },
  { id: 2, title: ' iBT ', des: '          . AL                  .' },
  { id: 3, title: '', des: '          .     ,         .' },
  { id: 4, title: '', des: '        .' },
  { id: 5, title: '', des: '3     .' },
]

const TAB = [
  { id: 1 },
  { id: 2 },
  { id: 3 },
  { id: 4 },
  { id: 5 },
]

const Content = () => {

  const [IDX, setIDX] = useState()
  const [SWIPER, setSWIPER] = useState()

  useEffect(() => {
    setIDX(0)

  }, [])

  return (
    <section className="Content">
      <p>  </p>
      <h2> </h2>
      <Swiper
        className='outer'
        slidesPerView={3}
        loop={true}
        effect={"coverflow"}
        grabCursor={true}
        centeredSlides={true}
        coverflowEffect={
          {
            rotate: 0,
            stretch: 154,
            depth: 90,
            modifier: 1,
            slideShadows: true,
          }
        }
        modules={[EffectCoverflow]}
        // onSlideChange={(e) => console.log(e)}
        onSwiper={(swiper) => setSWIPER(swiper)}
        onRealIndexChange={(e) => setIDX(e.realIndex)}
      >
        {
          SLIDE.map((el, idx) => {
            return (
              <SwiperSlide className='page' key={el.id}>
                <figure className={el.id === 2 ? "on" : ""}>
                  <div>
                    <strong>{el.title}</strong>
                    <a href="#!"></a>
                  </div>
                  <div><img src={process.env.PUBLIC_URL + "/assets/images/content" + el.id + ".png"} alt="" /></div>
                  <p>{el.des}</p>
                </figure>
              </SwiperSlide>

            )
          })
        }
      </Swiper>
      <div className="slideTab">
        <ul>
          {
            TAB.map((el, idx) => {
              return (
                <li key={el.id} onClick={() => SWIPER.slideTo(idx + 3)} className={idx === IDX ? "on" : ""}></li>
                // https://velog.io/@moonelysian/Swiperjs-%EC%82%AC%EC%9A%A9-%EC%8B%9C-%EC%99%B8%EB%B6%80%EC%97%90%EC%84%9C-Swiper-control-%ED%95%98%EB%8A%94-%EB%B2%95  ref          
              )
            })
          }
        </ul>
      </div>
    </section>
  )
}

export default Content;