import { useState } from "react"

const IMG = [
  { id: 1 },
  { id: 2 },
  { id: 3 },
  { id: 4 },
  { id: 5 },
  { id: 6 },
  { id: 7 },
]

const TXT = [
  { id: 1, grade: "1", name: " ", title: " !", content: "  ,           18  ! ,       ,          ^^" },
  { id: 2, grade: "3", name: " ", title: "  !", content: "           .           .     , 4                ^^" },
  { id: 3, grade: "5", name: " ", title: "   ", content: "                  .      '            '    . ^^" },
  { id: 4, grade: "4", name: " ", title: "  ", content: "                .        ~      2~3   ^^" },
  { id: 5, grade: "1", name: " ", title: " ,  ~", content: "     1 2   .    ,      .              !" },
  { id: 6, grade: "2", name: " ", title: "    !", content: "           .          ,              ." },
  { id: 7, grade: "4", name: " ", title: " 100 !!", content: "          ,     .     ~     100 ! !" },
]

const Review = () => {

  const [NUM, setNUM] = useState(1)
  return (
    <section className="Review">
      <div className="container">
        <p> </p>
        <h2> </h2>
        <div className="box">
          {
            IMG.map((el, idx) =>
              <div
                className={el.id === NUM ? "on" : ""}
                key={el.id}
                onClick={() => setNUM(idx + 1)}
              >
                <img src={process.env.PUBLIC_URL + "/assets/images/Screenshot" + el.id + ".png"} alt="" />
              </div>
            )
          }
        </div>
        <div className="text">
          {
            TXT.map((el, idx) => {
              return (
                <div className={el.id === NUM ? "on" : ""} key={el.id}>
                  <span>{el.grade}</span>
                  <span>{el.name}</span>
                  <div className="inner">
                    <h3>{el.title}</h3>
                    <p>{el.content}</p>
                  </div>

                </div>
              )
            })
          }
        </div>
      </div>

    </section>
  )
}

export default Review;